import React from 'react';
import List from '../components/List';

function History() {         // 全用函数组件
    return(
        <>                
            <h1>History</h1>
            <List></List>
        </>
    );                   // 在组件render中，必须用一个标签包裹起来，即使是空标签
}

export default History;